import React, { Component } from 'react';
import logo from './EMS.png';
import './App.css';
import Clock from '../../components/clock';
import Novartis from './novartis.png'
import ButtonBases from '../../common/ButtonBases'


class Home extends Component {
  constructor(props) {
        super(props);
        this.state = { 
          endDate: 'June, 21, 2018',
          yihong: 'May, 01, 2019',
          my: 'May, 01, 2200',
          rj: 'September, 6, 2069'
        };
   }
  componentDidMount() {
    document.title = "Novartis";
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Novartis Interns Workspace</h1>
        </header>
        <img src={Novartis} className="Clock-bg" alt="logo" />
        <div className="Clock-title">IA ending in </div>

        <Clock endDate={ this.state.endDate } style={{alignSelf: 'center'}}/>

        <ButtonBases/>
        <div className="Clock-title">Yi Hong's first class honourssssss</div>
        <Clock endDate={ this.state.yihong } style={{alignSelf: 'center'}}/>
        <div className="Clock-title">Last girl (that MY will ever slay) appears</div>
        <Clock endDate={ this.state.my } style={{alignSelf: 'center'}}/>
        <div className="Clock-title">RJ stops Uvuvwevwewevvwe-ing</div>
        <Clock endDate={ this.state.rj } style={{alignSelf: 'center'}}/>
      </div>
    );
  }
}

export default Home;



