import React, { Component } from 'react';
import './clock.css';
import Tooltip from 'material-ui/Tooltip';
import { ProgressBar } from 'react-bootstrap'
class Clock extends Component {
     constructor(props) {
     super(props);
     this.state = {
               days: 0,
               hours: 0,
               minutes: 0,
               seconds: 0,
               progress: 0
          };
     }
     componentWillMount() {
          this.getTimeUntil(this.props.endDate);
     }
     componentDidMount() {
          setInterval(() => this.getTimeUntil(this.props.endDate), 1000);
     }  
     leading0(num) {
          return num < 10 ? '0' + num : num;
     }
     getTimeUntil(endDate) {
          const time = Date.parse(endDate) - Date.parse(new Date());
          const totalTime = Date.parse(endDate) - Date.parse('January, 08, 2018');
          if (time < 0) {
               this.setState({ days: 0, hours: 0, minutes: 0, seconds: 0, progress: 100 });
          
          } else {
               const seconds = Math.floor((time/1000)%60);
               const minutes = Math.floor((time/1000/60)%60);
               const hours = Math.floor((time/(1000*60*60))%24);
               const days = Math.floor(time/(1000*60*60*24));
               this.setState({ 
                 days, 
                 hours, 
                 minutes, 
                 seconds,
                 progress: 100 - (time/totalTime) * 100
              });
          }



     }
     render() {
          return(
            <div className='clock-container'>
              <ProgressBar style={{positive: 'relative'}} active now={this.state.progress}
              bsStyle={this.state.progress < 10 ? "danger" : this.state.progress < 50 ? "warning" : "success"}/>
              <Tooltip id="tooltip-top" 
              title={`Date: ${this.props.endDate}`} placement="top">
                <div className="Clock" style={{flexDirection: 'row'}}>
                
                      <div className="Clock-number">
                          {this.leading0(this.state.days)} &nbsp;
                      </div>
                      <div className="Clock-unit">
                          Days &nbsp;
                      </div>
                      <div className="Clock-number">
                          {this.leading0(this.state.hours)} &nbsp;
                      </div>
                      <div className="Clock-unit">
                          Hours &nbsp;
                      </div>
                      <div className="Clock-number">
                          {this.leading0(this.state.minutes)} &nbsp;
                      </div>
                      <div className="Clock-unit">
                          Minutes &nbsp;
                      </div>
                      <div className="Clock-number">
                          {this.leading0(this.state.seconds)} &nbsp;
                      </div>
                      <div className="Clock-unit">
                          Seconds  &nbsp;
                      </div>
                </div>
              </Tooltip>
             </div>
          );
     }
}
export default Clock;


